import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { ClienteService } from "../../services/cliente.service";
import { UsuarioService } from "../../services/usuario.service";
import { Cliente } from "../../models/cliente";
import { Usuario } from "../../models/usuario";
import { Router } from "@angular/router";
import { VentaService } from "../../services/venta.service";
import { Venta } from "../../models/venta";
import { Detalle } from "../../models/detalle";

@Component({
  selector: 'app-venta',
  templateUrl: './venta.component.html',
  styleUrls: ['./venta.component.css']
})
export class VentaComponent implements OnInit {
  listadet:Detalle[]=[];
  listaclient:Cliente[]=[];
  listcli:Cliente[]=[];
  listausers:Usuario[]=[];
  cli:Cliente=new Cliente('','','','','','','',1,'');
 vent:Venta = new Venta('','','','',0,1,this.listadet,'');
  lista:Venta[]=[];
  action:boolean=false;
  form2:boolean=false;//para abrir nuevo cliente
  validationsform = this.formBuilder.group({
    idcliente: new FormControl('', Validators.required),
    razonsocial: new FormControl('', Validators.required),
    fecha: new FormControl('', Validators.required),
    total: new FormControl('', Validators.required),
    estado: new FormControl('', Validators.required)
  });

  constructor(public formBuilder:FormBuilder,
    public https:ClienteService,
    public http:VentaService,
    public httpss:UsuarioService,
    public router:Router,) { }

  ngOnInit() {
    console.log(localStorage.getItem('token'))
    if(localStorage.getItem('token')==null){
      this.router.navigate(['login']);
    }
    this.listar();
    this.listaclientes();
    this.listauserss();
  }
  buscarCliente(termino:string){
    if ( termino.length <= 0 ) {
      return;
    }
    this.https.buscarCliente( termino )
            .subscribe( serve =>
              this.listcli = serve.datos);

  }
  confirmar(cliente:Cliente){
    this.cli=cliente;
    console.log("estructura",this.cli)
  }
  listaclientes(){
    this.https.listar().subscribe(serve=>{
      this.listaclient=serve.datos;
      console.log("listaa datos", this.listaclient)
    });
  }
  listauserss(){
    this.httpss.listar().subscribe(serve=>{
      this.listausers=serve.datos;
      console.log("listaa datos", this.listausers)
    });
  }
  listar(){
    this.http.listar().subscribe(serve=>{
      console.log("sad",serve.datos)
      this.lista=serve.datos;
    });
    console.log(this.vent);
  }
  

  salir(){
    localStorage.removeItem('token');
  }
  nuevo(){
    if(this.action){
      this.http.editar(this.vent).subscribe(dato=>{
        if(dato.estado==1){
          this.form2=false;
          this.action=false;
        }
      });
      this.listar();
  }else{
    console.log(this.vent);
    delete this.vent._id;
    this.http.nuevo(this.vent).subscribe(serve=>{
      if(serve.estado){
        this.lista.push(serve.dato);
        //jQuery('#exampleModalCenter').modal('hide');
      }
    });
  }
  this.validationsform.reset();
  this.listar();
  }
  confirmado(){
    this.http.borrar(this.select).subscribe(s=>{
      this.lista.splice(
        this.lista.indexOf(this.select)
        ,1)
    });
  }
  mostrar(t){
    this.action=t;
  }
  select:Venta;
  borrar(item){
    this.select=item;
  }

  editar(item){
    this.form2=true;
    this.select=item;
    this.action=true;
    console.log(item);
    this.http.cargaractivo(item._id).subscribe(activo =>{
      if(activo.bien == 1){
        console.log("datoos viniendo del backend", activo.datos)
        this.vent=activo.datos;
        
      }
    })
  }

}
