import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { Observable } from'rxjs';
import { map, filter, switchMap } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class PlanificacionpService {
  //direccion del servidor
  url:String="http://localhost:3000/planificacionp";

  constructor(public http:HttpClient) { }
  listar():Observable<any>{
    
    return this.http.get(this.url+'/lista/'
    +localStorage.getItem('token'));
  }
  nuevo(planip):Observable<any>{
    return this.http.post(this.url+'/insertar',planip);
  }
  editar(planip):Observable<any>{
    return this.http.put(this.url+'/editar/'+ planip._id,planip);
  }
  borrar(planip):Observable<any>{
    return this.http.delete(this.url+'/borrar/'+planip._id);
  }
  cargaractivo(id:string){
    let url = this.url +'/'+id;
    return this.http.get(url).pipe(map( (resp:any)=>resp))
  }
}


