import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { ActivoService } from "../../services/activo.service";
import { Activo } from "../../models/activo";
import { Router } from "@angular/router";
@Component({
  selector: 'app-activo',
  templateUrl: './activo.component.html',
  styleUrls: ['./activo.component.css']
})
export class ActivoComponent implements OnInit {
  acti:Activo=new Activo('','','','','',1,'');
  lista:Activo[]=[];
  action:boolean=false;
  form2:boolean=false;//para abrir nuevo cliente
  validationsform = this.formBuilder.group({
    codigo: new FormControl('', Validators.required),
    objeto: new FormControl('', Validators.required),
    descripsion: new FormControl('', Validators.required),
    observaciones: new FormControl('', Validators.required),
    condicion: new FormControl('', Validators.required),
    estado: new FormControl('', Validators.required),
  });
  constructor(public formBuilder:FormBuilder,
    public http:ActivoService,
    public router:Router,) { }

  ngOnInit() {
    console.log(localStorage.getItem('token'))
    if(localStorage.getItem('token')==null){
      this.router.navigate(['login']);
    }
this.listar();
  }
  listar(){
    this.http.listar().subscribe(serve=>{
      this.lista=serve.datos;
    });
    console.log(this.acti);
  }
  

  salir(){
    localStorage.removeItem('token');
  }
  nuevo(){
    if(this.action){
      this.http.editar(this.acti).subscribe(dato=>{
        if(dato.estado==1){
          this.form2=false;
          this.action=false;
        }
      });
      this.listar();
  }else{
    console.log(this.acti);
    delete this.acti._id;
    this.http.nuevo(this.acti).subscribe(serve=>{
      if(serve.estado){
        this.lista.push(serve.dato);
        //jQuery('#exampleModalCenter').modal('hide');
      }
    });
  }
  this.validationsform.reset();
  this.listar();
  }
  confirmado(){
    this.http.borrar(this.select).subscribe(s=>{
      this.lista.splice(
        this.lista.indexOf(this.select)
        ,1)
    });
  }
  mostrar(t){
    this.action=t;
  }
  select:Activo;
  borrar(item){
    this.select=item;
  }

  editar(item){
    this.form2=true;
    this.select=item;
    this.action=true;
    console.log(item);
    this.http.cargaractivo(item._id).subscribe(activo =>{
      if(activo.bien == 1){
        console.log("datoos viniendo del backend", activo.datos)
        this.acti=activo.datos;
        
      }
    })
  }

}
