import { Component, OnInit,ElementRef, ViewChild } from '@angular/core';
import { io } from 'socket.io-client';
declare var MediaRecorder: any;
var mediaRecorder:any;
//declare var MediaRecorder: any;
declare var jQuery: any;
@Component({
  selector: 'app-radio',
  templateUrl: './radio.component.html',
  styleUrls: ['./radio.component.css']
})
export class RadioComponent implements OnInit {
  //@ViewChild('audio') audio: ElementRef<HTMLAudioElement>;
  constructor() { }
  blob_audio=[];
  ngOnInit() {
    
    (function ($) {
      $(document).ready(function(){
        const socket = io("http://192.168.1.11:3000/");
          var audio= (document.getElementById('audio') as HTMLAudioElement);
          //document.getElementById('audio');
      
          audio.addEventListener('progress', function() {
                
              var duration = 
              (document.getElementById('audio') as HTMLAudioElement).duration;
              //audio.duration;
              if (duration > 0) {
              for (var i = 0; i < audio.buffered.length; i++) {
                      if (audio.buffered.start(audio.buffered.length - 1 - i) < audio.currentTime) {
                          document.getElementById("buffered-amount").style.width = (audio.buffered.end(audio.buffered.length - 1 - i) / duration) * 100 + "%";
                          break;
                      }
                  }
              }
          });
          audio.addEventListener('timeupdate', function() {
              var duration =  audio.duration;
              if (duration > 0) {
              document.getElementById('progress-amount').style.width = ((audio.currentTime / duration)*100) + "%";
              }
          });
      
      
      
      socket.on('voice', function(arrayBuffer) {
          //blob_audio.push(arrayBuffer);
          var blob = new Blob(//blob_audio
          [arrayBuffer]
          , { 'type' : 'audio/ogg; codecs=opus' });
      
          audio.src = window.URL.createObjectURL(blob);
          audio.autoplay = true;
          console.log("play");
      });
        console.log("Hello from jQuery!");
      });
    })(jQuery);
    /*
    */
  }

}
