import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { PlanificacionService } from "../../services/planificacion.service";
import { ProgramaService } from "../../services/programa.service";
import { Programa } from "../../models/programa";
import { Planificacion } from "../../models/planificacion";
import { Router } from "@angular/router";
import { UsuarioService } from "../../services/usuario.service";
import { Usuario } from "../../models/usuario";
import { PlanificacionpService } from "../../services/planificacionp.service";
import { Planificacionp } from "../../models/planificacionp";
@Component({
  selector: 'app-planificacionp',
  templateUrl: './planificacionp.component.html',
  styleUrls: ['./planificacionp.component.css']
})
export class PlanificacionpComponent implements OnInit {
  planip:Planificacionp=new Planificacionp('','','','','','','',1,'','','','');
  listausers:Usuario[]=[];
  lista:Planificacionp[]=[];
  listaprog:Programa[]=[];
  listaplani:Planificacion[]=[];
  action:boolean=false;
  form2:boolean=false;//para abrir nuevo cliente
  validationsform = this.formBuilder.group({
    date: new FormControl('', Validators.required),
    tema1: new FormControl('', Validators.required),
    puntos1: new FormControl('', Validators.required),
    tema2: new FormControl('', Validators.required),
    puntos2: new FormControl('', Validators.required),
    tema3: new FormControl('', Validators.required),
    puntos3: new FormControl('', Validators.required),
    estado: new FormControl('', Validators.required),
    idprograma: new FormControl('', Validators.required),
    idplanificacion: new FormControl('', Validators.required),
    idusuario: new FormControl('', Validators.required),
  });
  constructor(public formBuilder:FormBuilder,
    public http:PlanificacionpService,
    public httpp_:PlanificacionService,
    public https:UsuarioService,
    public http_:ProgramaService,
    
    public router:Router,) { }

  ngOnInit() {
    console.log(localStorage.getItem('token'))
    if(localStorage.getItem('token')==null){
      this.router.navigate(['login']);
    }
    this.listar();
    this.listarprog();
    this.listarplani();
    this.listarusers();
  }
  listarusers(){
    this.https.listar().subscribe(serve=>{
      this.listausers=serve.datos;
    });
  }
  listarplani(){
    this.httpp_.listar().subscribe(serve=>{
      this.listaplani=serve.datos;
    });
  }
  listarprog(){
    this.http_.listar().subscribe(serve=>{
      this.listaprog=serve.datos;
      console.log("listaa datos", this.listaprog)
    });
  }
  listar(){
    this.http.listar().subscribe(serve=>{
      this.lista=serve.datos;
    });
    console.log(this.planip);
  }
  

  salir(){
    localStorage.removeItem('token');
  }
  nuevo(){
    if(this.action){
      this.http.editar(this.planip).subscribe(dato=>{
        if(dato.estado==1){
          this.form2=false;
          this.action=false;
        }
      });
      this.listar();
  }else{
    console.log(this.planip);
    delete this.planip._id;
    this.http.nuevo(this.planip).subscribe(serve=>{
      if(serve.estado){
        this.lista.push(serve.dato);
        //jQuery('#exampleModalCenter').modal('hide');
      }
    });
  }
  this.validationsform.reset();
  this.listar();
  }
  confirmado(){
    this.http.borrar(this.select).subscribe(s=>{
      this.lista.splice(
        this.lista.indexOf(this.select)
        ,1)
    });
  }
  mostrar(t){
    this.action=t;
  }
  select:Planificacionp;
  borrar(item){
    this.select=item;
  }

  editar(item){
    this.form2=true;
    this.select=item;
    this.action=true;
    console.log(item);
    this.http.cargaractivo(item._id).subscribe(activo =>{
      if(activo.bien == 1){
        console.log("datoos viniendo del backend", activo.datos)
        this.planip=activo.datos;
        
      }
    })
  }

}

