import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { UsuarioService } from "../../services/usuario.service";
import { Usuario } from "../../models/usuario";
import { Router } from "@angular/router";
@Component({
  selector: 'app-usuario',
  templateUrl: './usuario.component.html',
  styleUrls: ['./usuario.component.css']
})
export class UsuarioComponent implements OnInit {
  usu:Usuario=new Usuario('','','','','','','','','','',1,'');
  lista:Usuario[]=[];
  action:boolean=false;
  form2:boolean=false;//para abrir nuevo cliente
  validationsform = this.formBuilder.group({
    codigousuario: new FormControl('', Validators.required),
    nombre: new FormControl('', Validators.required),
    appaterno: new FormControl('', Validators.required),
    apmaterno: new FormControl('', Validators.required),
    telefono: new FormControl('', Validators.required),
    direccion: new FormControl('', Validators.required),
    cedula: new FormControl('', Validators.required),
    cargo: new FormControl('', Validators.required),
    login: new FormControl('', Validators.required),
    password: new FormControl('', Validators.required),
    estado: new FormControl('', Validators.required),
  });
  constructor(public formBuilder:FormBuilder,
    public http:UsuarioService,
    public router:Router,) { }

  ngOnInit() {
    console.log(localStorage.getItem('token'))
    if(localStorage.getItem('token')==null){
      this.router.navigate(['login']);
    }
this.listar();
  }
  listar(){
    this.http.listar().subscribe(serve=>{
      this.lista=serve.datos;
    });
    console.log(this.usu);
  }
  

  salir(){
    localStorage.removeItem('token');
  }
  nuevo(){
    if(this.action){
      this.http.editar(this.usu).subscribe(dato=>{
        if(dato.estado==1){
          this.form2=false;
          this.action=false;
        }
      });
      this.listar();
  }else{
    console.log(this.usu);
    delete this.usu._id;
    this.http.nuevo(this.usu).subscribe(serve=>{
      if(serve.estado){
        this.lista.push(serve.dato);
        //jQuery('#exampleModalCenter').modal('hide');
      }
    });
  }
  this.validationsform.reset();
  this.listar();
  }
  confirmado(){
    this.http.borrar(this.select).subscribe(s=>{
      this.lista.splice(
        this.lista.indexOf(this.select)
        ,1)
    });
  }
  mostrar(t){
    this.action=t;
  }
  select:Usuario;
  borrar(item){
    this.select=item;
  }

  editar(item){
    this.form2=true;
    this.select=item;
    this.action=true;
    console.log(item);
    this.http.cargaractivo(item._id).subscribe(activo =>{
      if(activo.bien == 1){
        console.log("datoos viniendo del backend", activo.datos)
        this.usu=activo.datos;
        
      }
    })
  }

}
