import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { ClienteService } from "../../services/cliente.service";
import { Cliente } from "../../models/cliente";
import { Router } from "@angular/router";

@Component({
  selector: 'app-cliente',
  templateUrl: './cliente.component.html',
  styleUrls: ['./cliente.component.css']
})
export class ClienteComponent implements OnInit {
  cli:Cliente = new Cliente('','','','','','','',1,'');
  lista:Cliente[]=[];
  action:boolean=false;
  form2:boolean=false;//para abrir nuevo cliente
  validationsform = this.formBuilder.group({
    nombre: new FormControl('', Validators.required),
    appaterno: new FormControl('', Validators.required),
    apmaterno: new FormControl('', Validators.required),
    ci: new FormControl('', Validators.required),
    nit: new FormControl('', Validators.required),
    telefono: new FormControl('', Validators.required),
    direccion: new FormControl('', Validators.required),
    estado: new FormControl('', Validators.required),
  });
  constructor(public formBuilder:FormBuilder,
    public http:ClienteService,
    public router:Router,) { }

  ngOnInit() {
    console.log(localStorage.getItem('token'))
    if(localStorage.getItem('token')==null){
      this.router.navigate(['login']);
    }
this.listar();
  }
  listar(){
    this.http.listar().subscribe(serve=>{
      this.lista=serve.datos;
    });
    console.log(this.cli);
  }
  

  salir(){
    localStorage.removeItem('token');
  }
  nuevo(){
    if(this.action){
      this.http.editar(this.cli).subscribe(dato=>{
        if(dato.estado==1){
          this.form2=false;
          this.action=false;
        }
      });
      this.listar();
  }else{
    console.log(this.cli);
    delete this.cli._id;
    this.http.nuevo(this.cli).subscribe(serve=>{
      if(serve.estado){
        this.lista.push(serve.dato);
        //jQuery('#exampleModalCenter').modal('hide');
      }
    });
  }
  this.validationsform.reset();
  this.listar();
  }
  confirmado(){
    this.http.borrar(this.select).subscribe(s=>{
      this.lista.splice(
        this.lista.indexOf(this.select)
        ,1)
    });
  }
  mostrar(t){
    this.action=t;
  }
  select:Cliente;
  borrar(item){
    this.select=item;
  }

  editar(item){
    this.form2=true;
    this.select=item;
    this.action=true;
    console.log(item);
    this.http.cargaractivo(item._id).subscribe(activo =>{
      if(activo.bien == 1){
        console.log("datoos viniendo del backend", activo.datos)
        this.cli=activo.datos;
        
      }
    })
  }

}
