import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { ProgramaService } from "../../services/programa.service";
import { Programa } from "../../models/programa";
import { Router } from "@angular/router";
import { UsuarioService } from "../../services/usuario.service";
import { Usuario } from "../../models/usuario";

@Component({
  selector: 'app-programa',
  templateUrl: './programa.component.html',
  styleUrls: ['./programa.component.css']
})
export class ProgramaComponent implements OnInit {
  listausers:Usuario[]=[];
  prog:Programa=new Programa('','','','',1,'','');
  lista:Programa[]=[];
  action:boolean=false;
  form2:boolean=false;//para abrir nuevo cliente
  validationsform = this.formBuilder.group({
    nombreprograma: new FormControl('', Validators.required),
    genero: new FormControl('', Validators.required),
    horainicio: new FormControl('', Validators.required),
    horafin: new FormControl('', Validators.required),
    estado: new FormControl('', Validators.required),
    idusuario: new FormControl('', Validators.required),
  });
  constructor(public formBuilder:FormBuilder,
    public http:ProgramaService,
    public https:UsuarioService,
    public router:Router,) { }

  ngOnInit() {
    console.log(localStorage.getItem('token'))
    if(localStorage.getItem('token')==null){
      this.router.navigate(['login']);
    }
this.listar();
this.listarusers()
  }
  listar(){
    this.http.listar().subscribe(serve=>{
      this.lista=serve.datos;
    });
    console.log(this.prog);
  }
  listarusers(){
    this.https.listar().subscribe(serve=>{
      this.listausers=serve.datos;
    });
    console.log("sdsasd",this.listausers)
  }

  salir(){
    localStorage.removeItem('token');
  }
  nuevo(){
    if(this.action){
      this.http.editar(this.prog).subscribe(dato=>{
        if(dato.estado==1){
          this.form2=false;
          this.action=false;
        }
      });
      this.listar();
  }else{
    console.log(this.prog);
    delete this.prog._id;
    this.http.nuevo(this.prog).subscribe(serve=>{
      if(serve.estado){
        this.lista.push(serve.dato);
        //jQuery('#exampleModalCenter').modal('hide');
      }
    });
  }
  this.validationsform.reset();
  this.listar();
  }
  confirmado(){
    this.http.borrar(this.select).subscribe(s=>{
      this.lista.splice(
        this.lista.indexOf(this.select)
        ,1)
    });
  }
  mostrar(t){
    this.action=t;
  }
  select:Programa;
  borrar(item){
    this.select=item;
  }

  editar(item){
    this.form2=true;
    this.select=item;
    this.action=true;
    console.log(item);
    this.http.cargaractivo(item._id).subscribe(activo =>{
      if(activo.bien == 1){
        console.log("datoos viniendo del backend", activo.datos)
        this.prog=activo.datos;
        
      }
    })
  }

}
