import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { Observable } from 'rxjs';
import { map, filter, switchMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ClienteService {
  //direccion del servidor
  url:String="http://localhost:3000/cliente";

  constructor(public http:HttpClient) { }
  listar():Observable<any>{
    
    return this.http.get(this.url+'/lista/'
    +localStorage.getItem('token'));
  }
  nuevo(cli):Observable<any>{
    return this.http.post(this.url+'/insertar',cli);
  }
  editar(cli):Observable<any>{
    return this.http.put(this.url+'/editar/'+ cli._id,cli);
  }
  borrar(cli):Observable<any>{
    return this.http.delete(this.url+'/borrar/'+cli._id);
  }
  cargaractivo(id:string){
    let url = this.url +'/'+id;
    return this.http.get(url).pipe(map( (resp:any)=>resp))
  }
  buscarCliente( termino: string ) {
    let url = this.url + '/busqueda/' + termino;
    return this.http.get( url ).pipe(map( (resp: any) => resp ));
 
  }
}
