import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ClienteComponent } from './componentes/cliente/cliente.component';
import { LoginComponent } from './login/login.component';
import {MenuComponent} from './menu/menu.component';
import {ActivoComponent} from './componentes/activo/activo.component';
import {UsuarioComponent} from './componentes/usuario/usuario.component';
import {ProgramaComponent} from './componentes/programa/programa.component';
import {PlanificacionComponent} from './componentes/planificacion/planificacion.component';
import {PlanificacionpComponent} from './componentes/planificacionp/planificacionp.component';
import {ProgramacionComponent} from './componentes/programacion/programacion.component';
import {VentaComponent} from './componentes/venta/venta.component';

import {PaginaComponent} from './componentes/pagina/pagina.component';

import {TransmisionComponent} from './componentes/transmision/transmision.component';
import { RadioComponent } from './componentes/radio/radio.component';


const routes: Routes = [
  ///transmitir
  {path:'transmitir',component: TransmisionComponent },
  {path:'radio',component: RadioComponent },
  {path:'activo',component: ActivoComponent },
  {path:'usuario',component: UsuarioComponent },
  {path:'venta',component: VentaComponent },
  {path:'pagina',component: PaginaComponent },
  {path:'programa',component: ProgramaComponent },
  {path:'login',component: LoginComponent },
  {path:'cliente',component: ClienteComponent },
  {path:'planificacion',component: PlanificacionComponent },
  {path:'planificacionp',component: PlanificacionpComponent },
  {path:'programacion',component: ProgramacionComponent },
  {path:'',  redirectTo: 'producto', pathMatch: 'full'},
  {path:'menu', component:MenuComponent},
  { path: '**', component: MenuComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
